  .google-btn {
      margin: 25px;
      width: 80%;
      min-width: 184px;
      max-width: 184px;
      height: 42px;
      background-color: #fcfcfc;
      border-radius: 2px;
      box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .2);
      cursor: pointer;
      cursor: hand;
      align-self: center;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none;
      transition: all 400ms ease 0s;
      display: flex;
  }
  .google-btn .google-icon-wrapper {
      position: absolute;
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-radius: 2px;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none
  }
  .google-btn .google-icon-svg {
      position: absolute;
      margin-top: 11px;
      margin-left: 11px;
      width: 18px;
      height: 18px;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none
  }
  .google-btn .btn-text {
      float: right;
      margin: 11px 14px 40px 40px;
      color: #757575;
      font-size: 14px;
      letter-spacing: .2px;
      font-family: Roboto;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none
  }
  .google-btn:hover {
      box-shadow: 0 3px 8px rgba(117, 117, 117, .5);
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none
  }
  .google-btn:active {
      box-shadow: 0 1px 1px #757575;
      background: #F8F8F8;
      color: #fff;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none
  }
  
  .normal-btn {
      margin: 25px;
